"use client"

import { useEffect, useState, useCallback, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { 
  ChevronLeft, Send, Users, User, MessageSquare, 
  LogOut, Loader2, Sparkles, ShieldCheck, Hash 
} from "lucide-react"
import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'
import Image from "next/image"

export default function RealTimeChatPage() {
  const router = useRouter()
  const [userId, setUserId] = useState("")
  const [userName, setUserName] = useState("")
  
  const [activeTab, setActiveTab] = useState("GROUP") 
  const [rooms, setRooms] = useState([])
  const [currentRoom, setCurrentRoom] = useState<any>(null)
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  const [isLoadingHistory, setIsLoadingHistory] = useState(false)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)
  const subscriptions = useRef<Record<string, any>>({})

  const API_BASE = 'http://sparta-nextme.xyz:18080'

  const loadChatHistory = async (roomId: string) => {
    const token = localStorage.getItem("accessToken")
    setIsLoadingHistory(true)
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room/${roomId}/messages`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const rawMessages = data.result || data.messages || (Array.isArray(data) ? data : [])
      setMessages(rawMessages)
    } catch (e) { setMessages([]) } finally { setIsLoadingHistory(false) }
  }

  const loadRooms = useCallback(async (type: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=${type}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      setRooms(data.result || data.rooms || (Array.isArray(data) ? data : []))
    } catch (e) { console.error(e) }
  }, [])

  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    if (!token) return router.push("/login")
    
    setUserId(localStorage.getItem("userId") || "")
    setUserName(localStorage.getItem("name") || "사용자")

    const socket = new SockJS(`${API_BASE}/ws/chat`)
    const client = new Client({
      webSocketFactory: () => socket,
      connectHeaders: { Authorization: `Bearer ${token}` },
      onConnect: () => {
        setIsConnected(true)
        loadRooms(activeTab)
      },
    })
    client.activate(); stompClient.current = client
    return () => { client.deactivate() }
  }, [activeTab, loadRooms, router])

  const selectRoom = (room: any) => {
    setCurrentRoom(room)
    loadChatHistory(room.chatRoomId)
    if (!subscriptions.current[room.chatRoomId]) {
      stompClient.current?.subscribe(`/topic/chat.room.${room.chatRoomId}`, (msg) => {
        const received = JSON.parse(msg.body)
        setMessages((prev) => [...prev, received])
      })
      subscriptions.current[room.chatRoomId] = true
    }
  }

  useEffect(() => {
    if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight
  }, [messages])

  return (
    <div className="min-h-screen bg-background text-foreground flex flex-col overflow-hidden">
      {/* Header - 대시보드와 동일한 스타일 */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <Button variant="ghost" size="icon" onClick={() => router.push("/dashboard")}>
              <ChevronLeft className="h-5 w-5" />
            </Button>
            <Link href="/" className="flex items-center gap-2">
              <Image src="/logo.png" alt="Next Me" width={110} height={30} className="h-7 w-auto" />
            </Link>
          </div>
          <div className="flex items-center gap-4">
             <div className="flex items-center gap-2 px-3 py-1 bg-secondary/30 rounded-full border border-border/50">
                <div className={`h-2 w-2 rounded-full ${isConnected ? 'bg-green-500 animate-pulse' : 'bg-slate-500'}`} />
                <span className="text-[10px] font-bold text-muted-foreground uppercase">{isConnected ? "Online" : "Offline"}</span>
             </div>
             <Avatar className="h-8 w-8 border">
                <AvatarFallback className="bg-primary/5 text-xs font-bold">{userName?.charAt(0)}</AvatarFallback>
             </Avatar>
          </div>
        </div>
      </header>

      <div className="flex-1 flex container mx-auto px-0 md:px-4 overflow-hidden">
        {/* Sidebar - 사이드바 카테고리 스타일 */}
        <aside className="w-80 border-r border-border hidden md:flex flex-col bg-card/20 backdrop-blur-md">
          <div className="p-6">
            <h2 className="text-xs font-black mb-4 uppercase tracking-widest text-muted-foreground/60">Chat Channels</h2>
            <Tabs value={activeTab} onValueChange={(v) => setActiveTab(v)} className="w-full">
              <TabsList className="grid grid-cols-2 w-full bg-secondary/50 border border-border/50">
                <TabsTrigger value="GROUP" className="data-[state=active]:bg-background text-xs font-bold transition-all">그룹</TabsTrigger>
                <TabsTrigger value="DIRECT" className="data-[state=active]:bg-background text-xs font-bold transition-all">1:1</TabsTrigger>
              </TabsList>
            </Tabs>
          </div>

          <div className="flex-1 overflow-y-auto px-4 space-y-2 pb-6">
            {rooms.length === 0 ? (
              <div className="text-center py-10 opacity-30 italic text-xs font-medium">검색된 채널이 없습니다.</div>
            ) : (
              rooms.map((room: any) => (
                <button
                  key={room.chatRoomId}
                  onClick={() => selectRoom(room)}
                  className={`w-full p-4 rounded-xl flex items-center gap-3 transition-all border group ${
                    currentRoom?.chatRoomId === room.chatRoomId 
                    ? 'bg-primary/10 border-primary/40 shadow-sm' 
                    : 'bg-transparent border-transparent hover:bg-secondary/30'
                  }`}
                >
                  <div className={`h-10 w-10 rounded-lg flex items-center justify-center shrink-0 transition-colors ${
                    currentRoom?.chatRoomId === room.chatRoomId ? 'bg-primary text-white' : 'bg-secondary text-muted-foreground group-hover:bg-primary/20'
                  }`}>
                    {activeTab === "GROUP" ? <Hash className="h-5 w-5" /> : <User className="h-5 w-5" />}
                  </div>
                  <div className="text-left overflow-hidden">
                    <p className={`font-bold truncate text-sm ${currentRoom?.chatRoomId === room.chatRoomId ? 'text-foreground' : 'text-muted-foreground'}`}>{room.title}</p>
                    <p className="text-[10px] opacity-40 truncate font-mono">CHANNEL_ID_{room.chatRoomId.substring(0,6)}</p>
                  </div>
                </button>
              ))
            )}
          </div>
        </aside>

        {/* Main Chat Content */}
        <main className="flex-1 flex flex-col relative bg-background/50">
          {currentRoom ? (
            <>
              {/* Chat Room Header */}
              <div className="h-16 px-6 border-b border-border flex items-center justify-between bg-card/30">
                <div className="flex items-center gap-3">
                  <div className="h-8 w-8 rounded bg-primary/10 flex items-center justify-center"><Sparkles className="h-4 w-4 text-primary" /></div>
                  <h2 className="font-bold text-sm tracking-tight">{currentRoom.title}</h2>
                </div>
                <Button variant="ghost" size="sm" className="text-xs font-bold text-muted-foreground hover:text-destructive">
                  <LogOut className="h-4 w-4 mr-2" /> 나가기
                </Button>
              </div>

              {/* Message Area */}
              <div ref={scrollRef} className="flex-1 overflow-y-auto p-6 space-y-6 custom-scrollbar bg-gradient-to-b from-transparent to-secondary/5">
                {isLoadingHistory && (
                  <div className="flex justify-center p-4"><Loader2 className="h-5 w-5 animate-spin text-primary" /></div>
                )}
                {messages.map((msg, idx) => (
                  <div key={idx} className={`flex gap-3 ${msg.senderId === userId ? "justify-end" : "justify-start"} animate-in fade-in duration-300`}>
                    {msg.senderId !== userId && (
                      <Avatar className="h-8 w-8 border-2 border-background">
                        <AvatarFallback className="bg-secondary text-[10px] font-black">{msg.senderName?.charAt(0)}</AvatarFallback>
                      </Avatar>
                    )}
                    <div className={`flex flex-col space-y-1.5 max-w-[75%] ${msg.senderId === userId ? "items-end" : "items-start"}`}>
                      <span className="text-[10px] font-black text-muted-foreground/60 px-1 uppercase tracking-tighter">{msg.senderName}</span>
                      <div className={`p-3 text-sm font-medium shadow-sm ${
                        msg.senderId === userId 
                        ? "bg-primary text-primary-foreground rounded-2xl rounded-tr-none" 
                        : "bg-card border border-border/50 text-foreground rounded-2xl rounded-tl-none"
                      }`}>
                        {msg.content}
                      </div>
                    </div>
                  </div>
                ))}
              </div>

              {/* Input Area - 대시보드 인풋 스타일 */}
              <div className="p-4 bg-card/50 backdrop-blur-md border-t border-border">
                <form 
                  onSubmit={(e) => { e.preventDefault(); if(inputValue.trim()) { stompClient.current?.publish({ destination: `/app/chat.send/${currentRoom.chatRoomId}`, body: JSON.stringify({ content: inputValue, roomType: activeTab }) }); setInputValue(""); } }}
                  className="flex gap-2 max-w-4xl mx-auto"
                >
                  <Input 
                    value={inputValue}
                    onChange={(e) => setInputValue(e.target.value)}
                    placeholder="메시지를 입력하세요..." 
                    className="flex-1 h-12 bg-background border-border/50 focus-visible:ring-primary shadow-inner text-sm"
                  />
                  <Button type="submit" size="icon" className="h-12 w-12 rounded-xl shadow-lg shadow-primary/20 transition-transform active:scale-95" disabled={!inputValue.trim()}>
                    <Send className="h-5 w-5" />
                  </Button>
                </form>
              </div>
            </>
          ) : (
            <div className="flex-1 flex flex-col items-center justify-center p-8 text-center">
              <div className="relative mb-6">
                <div className="absolute inset-0 bg-primary/20 blur-3xl rounded-full" />
                <div className="relative bg-card border border-border p-6 rounded-3xl shadow-2xl">
                  <MessageSquare className="h-12 w-12 text-primary opacity-50" />
                </div>
              </div>
              <h3 className="text-lg font-bold mb-2">대화방이 선택되지 않았습니다</h3>
              <p className="text-xs text-muted-foreground max-w-[200px] font-medium leading-relaxed">
                좌측 목록에서 그룹 채팅이나 1:1 대화방을 선택하여 소통을 시작하세요.
              </p>
            </div>
          )}
        </main>
      </div>
    </div>
  )
}